import React, { useState } from 'react';
import { LoginForm } from './components/LoginForm';
import { StudySelector } from './components/StudySelector';
import { WorldMap } from './components/WorldMap';
import { SiteHealthDashboard } from './components/SiteHealthDashboard';
import { useVault } from './hooks/useVault';
import { STATUS_COLORS, COUNTRY_STATUS_COLORS } from './constants/colors';
import type { StatusColorConfig } from './constants/colors';
import './App.css';
import { ErrorBoundary } from 'react-error-boundary'

function ErrorFallback() {
  return (
    <div className="error-container">
      <h2></h2>
      <button onClick={() => window.location.reload()}>
        
      </button>
    </div>
  );
}


// 
const defaultStatusColors: StatusColorConfig[] = [
  { name: '', siteStatus: 'active_site__v', statusColor: STATUS_COLORS.ACTIVE },
  { name: '', siteStatus: 'completed_site__v', statusColor: STATUS_COLORS.COMPLETED },
  { name: '', siteStatus: 'suspended_site__v', statusColor: STATUS_COLORS.SUSPENDED },
  { name: '', siteStatus: 'terminated_site__v', statusColor: STATUS_COLORS.TERMINATED },
  { name: '', siteStatus: 'on_hold__v', statusColor: STATUS_COLORS.ON_HOLD },
  { name: '', siteStatus: 'inactive_site__v', statusColor: STATUS_COLORS.INACTIVE }
];

// 
const defaultCountryStatusColors = {
  ACTIVE: COUNTRY_STATUS_COLORS.ACTIVE,
  INACTIVE: COUNTRY_STATUS_COLORS.INACTIVE,
  ARCHIVED: COUNTRY_STATUS_COLORS.ARCHIVED
};

const App: React.FC = () => {
  const {
    isLoggedIn,
    studies,
    selectedStudy,
    sites,
    countries,
    statusColors = defaultStatusColors,
    loading,
    error,
    handleLogin,
    handleLogout,
    selectStudy
  } = useVault();

  // 
  const [activeView, setActiveView] = useState<'map' | 'health'>('map');

  // 
  const handleError = (error: Error) => {
    console.error('Application error:', error);
    // 
  };

  if (!isLoggedIn) {
    return (
      <LoginForm
        onLogin={handleLogin}
        error={error}
        loading={loading}
      />
    );
  }

  return (
    <ErrorBoundary FallbackComponent={ErrorFallback}>
      <div className="app">
        <header className="app-header">
          <h1>WonderDrugs Study Map</h1>
          <div className="view-controls">
            <button
              className={`view-button ${activeView === 'map' ? 'active' : ''}`}
              onClick={() => setActiveView('map')}
            >
              
            </button>
            <button
              className={`view-button ${activeView === 'health' ? 'active' : ''}`}
              onClick={() => setActiveView('health')}
            >
              
            </button>
          </div>
          <button onClick={handleLogout} className="logout-button">
            
          </button>
        </header>
        <main className="app-main">
          <aside className="app-sidebar">
            <StudySelector
              studies={studies || []}
              selectedStudy={selectedStudy}
              onSelect={selectStudy}
              loading={loading}
            />
          </aside>
          <section className="map-section">
            {loading ? (
              <div className="loading">
                <span className="spinner"></span>
                ...
              </div>
            ) : (
              <>
                {activeView === 'map' && (
                  <WorldMap
                    sites={sites || []}
                    countries={countries || []}
                    statusColors={Array.isArray(statusColors) ? statusColors : defaultStatusColors}
                    selectedStudy={selectedStudy?.id || null}
                  />
                )}
                {activeView === 'health' && (
                  <SiteHealthDashboard
                    studyId={selectedStudy?.id || null}
                    loading={loading}
                  />
                )}
              </>
            )}
          </section>
        </main>
        {error && (
          <div className="error-message">
            <span className="error-icon"></span>
            {typeof error === 'string' ? error : error.message}
          </div>
        )}
      </div>
    </ErrorBoundary>
  );
};

export default App;
